import * as React from "react";
import Link from "next/link";
import { ArrowRight } from "lucide-react";
import { ProsfinButton, type ProsfinButtonProps } from "./brand-button";
import { cn } from "@/lib/utils";

export interface ProsfinLinkButtonProps
  extends Omit<ProsfinButtonProps, "brandVariant" | "variant"> {
  /**
   * Optional href for Link
   */
  href?: string;
  /**
   * Children content
   */
  children: React.ReactNode;
  /**
   * Show arrow icon
   * @default true
   */
  showArrow?: boolean;
}

/**
 * ProsfinLinkButton - Link button wrapper
 * 
 * Gi·ªëng text link, nh∆∞ng d√πng c·∫•u tr√∫c button (d·ªÖ reuse v·ªõi router).
 * D√πng ·ªü copy ki·ªÉu "T√¨m hi·ªÉu th√™m v·ªÅ c√°ch l√†m vi·ªác c·ªßa ProsFIN ‚Üí"
 * Wrapper component kh√¥ng ch·ªânh s·ª≠a shadcn components tr·ª±c ti·∫øp.
 */
export function ProsfinLinkButton({
  href,
  children,
  showArrow = true,
  className,
  ...props
}: ProsfinLinkButtonProps) {
  const content = (
    <>
      {children}
      {showArrow && <ArrowRight className="ml-1 h-4 w-4" />}
    </>
  );

  const button = (
    <ProsfinButton
      variant="link"
      className={cn("font-normal", className)}
      {...props}
    >
      {content}
    </ProsfinButton>
  );

  if (href) {
    return (
      <Link href={href} className="inline-block">
        {button}
      </Link>
    );
  }

  return button;
}

