import * as React from "react";
import Link from "next/link";
import { ProsfinButton, type ProsfinButtonProps } from "./brand-button";
import { cn } from "@/lib/utils";

export interface ProsfinDestructiveButtonProps
  extends Omit<ProsfinButtonProps, "brandVariant" | "variant"> {
  /**
   * Optional href for Link
   */
  href?: string;
  /**
   * Children content
   */
  children: React.ReactNode;
}

/**
 * ProsfinDestructiveButton - Destructive button wrapper
 * 
 * D√πng cho c√°c action nguy hi·ªÉm nh∆∞ x√≥a, unsubscribe.
 * √çt d√πng tr√™n landing, nh∆∞ng c·∫ßn c√≥ trong system.
 * Wrapper component kh√¥ng ch·ªânh s·ª≠a shadcn components tr·ª±c ti·∫øp.
 */
export function ProsfinDestructiveButton({
  href,
  children,
  className,
  ...props
}: ProsfinDestructiveButtonProps) {
  const button = (
    <ProsfinButton
      variant="destructive"
      className={cn("font-medium", className)}
      {...props}
    >
      {children}
    </ProsfinButton>
  );

  if (href) {
    return (
      <Link href={href} className="inline-block">
        {button}
      </Link>
    );
  }

  return button;
}

