"use client";

import * as React from "react";

export interface ToastOptions {
  title?: string;
  description: string;
  variant?: "default" | "success" | "error" | "warning";
  duration?: number;
}

interface ToastContextValue {
  toast: (options: ToastOptions) => void;
}

const ToastContext = React.createContext<ToastContextValue | undefined>(
  undefined
);

/**
 * useProsfinToast - Hook ƒë·ªÉ s·ª≠ d·ª•ng toast
 * 
 * Hook ƒë·ªÉ hi·ªÉn th·ªã toast notifications.
 * Hi·ªán t·∫°i d√πng console.log v√† alert, c√≥ th·ªÉ n√¢ng c·∫•p v·ªõi Radix UI Toast sau.
 */
export function useProsfinToast() {
  const context = React.useContext(ToastContext);
  if (!context) {
    // Fallback: d√πng console v√† alert
    return {
      toast: (options: ToastOptions) => {
        console.log("Toast:", options);
        if (options.variant === "error") {
          alert(`L·ªói: ${options.description}`);
        } else {
          alert(options.description);
        }
      },
    };
  }
  return context;
}

/**
 * ProsfinToastProvider - Toast provider component
 * 
 * Provider cho toast system.
 * Hi·ªán t·∫°i ch·ªâ l√† wrapper, c√≥ th·ªÉ n√¢ng c·∫•p v·ªõi Radix UI Toast sau.
 * Wrapper component kh√¥ng ch·ªânh s·ª≠a shadcn components tr·ª±c ti·∫øp.
 */
export function ProsfinToastProvider({
  children,
}: {
  children: React.ReactNode;
}) {
  const toast = React.useCallback((options: ToastOptions) => {
    // TODO: Implement v·ªõi Radix UI Toast khi c√≥ dependency
    console.log("Toast:", options);
    if (options.variant === "error") {
      alert(`L·ªói: ${options.description}`);
    } else {
      alert(options.description);
    }
  }, []);

  return (
    <ToastContext.Provider value={{ toast }}>
      {children}
    </ToastContext.Provider>
  );
}

