"use client";

import * as React from "react";
import { cn } from "@/lib/utils";
import { AnimatedCounter } from "@/components/shared/animation/animated-counter";
import { Text } from "@/components/shared";

export interface ProsfinMetricPillProps {
  /**
   * Label text
   */
  label: string;
  /**
   * Value text (s·ªë, text ng·∫Øn)
   * N·∫øu l√† s·ªë, s·∫Ω ƒë∆∞·ª£c animate t·ª´ 0
   */
  value: string;
  /**
   * Enable animated counter (ch·ªâ khi value l√† s·ªë)
   * @default true
   */
  enableAnimation?: boolean;
  /**
   * Additional className
   */
  className?: string;
}

/**
 * ProsfinMetricPill - Component hi·ªÉn th·ªã metric/highlight d·∫°ng pill
 * 
 * S·ª≠ d·ª•ng cho highlights, stats, trust indicators.
 * Style: pill v·ªõi label v√† value r√µ r√†ng.
 * H·ªó tr·ª£ animated counter khi value l√† s·ªë.
 * 
 * Wrapper component kh√¥ng ch·ªânh s·ª≠a shadcn components tr·ª±c ti·∫øp.
 */
export function ProsfinMetricPill({
  label,
  value,
  enableAnimation = true,
  className,
}: ProsfinMetricPillProps) {
  // Parse value ƒë·ªÉ x√°c ƒë·ªãnh c√≥ ph·∫£i s·ªë kh√¥ng
  const parseValue = (val: string): { num: number | null; prefix: string; suffix: string } => {
    // Match patterns like "120+", "8+", "50+ DN", "+120", "120"
    const match = val.match(/^([+\-]?)(\d+(?:\.\d+)?)(.*)$/);
    if (match) {
      return {
        num: parseFloat(match[2]),
        prefix: match[1] || "",
        suffix: match[3] || "",
      };
    }
    return { num: null, prefix: "", suffix: "" };
  };

  const parsed = parseValue(value);
  const shouldAnimate = enableAnimation && parsed.num !== null;

  return (
    <div
      className={cn(
        "flex flex-col items-center justify-center rounded-lg border bg-card p-4 text-center shadow-sm transition-shadow hover:shadow-md",
        className
      )}
    >
      <Text as="div" variant="metric" className="mb-1">
        {shouldAnimate ? (
          <AnimatedCounter
            value={parsed.num!}
            prefix={parsed.prefix}
            suffix={parsed.suffix}
            duration={2000}
          />
        ) : (
          value
        )}
      </Text>
      <Text as="div" variant="caption">
        {label}
      </Text>
    </div>
  );
}

